import 'react-app-polyfill/ie11';
import 'react-app-polyfill/stable';
import '../src/github.css';
const React = require('react')
const ReactDOM = require('react-dom')
const ReactMarkdown = require('react-markdown')
// https://tokenyet.github.io/MinecraftScepterVersioner/
// const input = require('./content.js').default;

// const superagent = require('superagent');
const {Ajax} = require('react-superagent');

ReactDOM.render(
  <Ajax url='https://raw.githubusercontent.com/Tokenyet/MinecraftScepterVersioner/master/content.md' method='get'>{
    ({error, response, done}) => !done ?
      <div>loading...</div> :
      <ReactMarkdown source={response.text} />
  }</Ajax>
, document.getElementById('root'))